.wizard-wrapper {
    position: fixed;
    width: 100vw; height: 100vh;
    background: rgba( 39, 43, 51, 0.7 );
    color: rgb( 166, 178, 192 );
    z-index: 1000;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-line-pack: center;
        align-content: center;
    -ms-flex-align: center;
        align-items: center;
}

.wizard {
	background: rgb( 39, 43, 51 );
	-webkit-box-shadow: rgba(0, 0, 0, 0.5) 0px 0px 30px 0px;
	        box-shadow: rgba(0, 0, 0, 0.5) 0px 0px 30px 0px;
    padding: 25px;
    border-radius: 5px;
}


/** Responsive */
@media screen and ( max-aspect-ratio: 13 / 9 ) { /** Portrait */
	.wizard {
		width: calc( 90vw - 90px ); height: calc( 130vw - 130px );
		max-height: 90vh;
	}
}
@media screen and ( min-aspect-ratio: 13 / 9 ) { /** Landscape */
	.wizard {
		width: calc( 130vh - 130px ); height: calc( 90vh - 90px );
		max-width: 90vw;
	}
}



