.editor {
	font-family: IranSans;
	height: 100vh;
	display: -ms-flexbox;
	display: flex;
	z-index: 1;
}


/** Responsive */
@media screen and ( max-aspect-ratio: 13 / 9 ) { /** Portrait */
	.editor {
		-ms-flex-direction: column;
		    flex-direction: column;
	}

	.editor .result-wrapper {
		width: 100vw;
	}

	.editor .controls-wrapper {
		width: 100vw;
	}

	.editor .controls {
		padding-top: 30px !important;
	}
}
@media screen and ( min-aspect-ratio: 13 / 9 ) { /** Landscape */
	.editor {
		-ms-flex-direction: row;
		    flex-direction: row;
	}

	.editor .result-wrapper {
		height: 100vh;
	}

	.editor .controls-wrapper {
		height: 100vh;
	}
}


/** Result */
.editor .result-wrapper {
	-ms-flex: 62 62 100%;
	    flex: 62 62 100%;
	-webkit-box-shadow: rgba(0, 0, 0, 1) 10px 10px 100px 10px;
	        box-shadow: rgba(0, 0, 0, 1) 10px 10px 100px 10px;
	z-index: 10;
}

.editor .result {
	margin: 15px;
	outline: 2px solid #CCC;
	overflow: hidden;
}

/** Controls */
.editor .controls-wrapper {
	-ms-flex: 38 38 100%;
	    flex: 38 38 100%;
	background: rgba( 39, 43, 51, 0.9 );
	color: rgb( 166, 178, 192 );
	font-family: Behdad;
	font-size: 0.3rem;
	overflow: auto;
}

.editor .controls label {
	display: block;
	padding: 10px 20px;
	padding-bottom: 15px;
	text-align: right;
}

.editor .controls .control {
	width: 100%;
	margin: 0px;
	margin-bottom: 15px;
}

.editor .controls .control[ type="text" ] {
	border: none;
	border-radius: 5px;
	background: rgba( 255, 255, 255, 0.1 );
	color: #FAFAFA;
	font-family: IranSans;
	text-align: right;
	font-size: 0.4rem;
	padding: 10px 25px;
	margin: 10px;
	margin-top: 0px;
	margin-bottom: 35px;
	width: calc( 100% - 20px );
}


/* Spacing hack */
.color:last-child, .emoji:last-child, .font:last-child {
	margin-left: 15px !important;
}

.slider {
	padding: 0px 15px;
	margin-top: -15px !important;
}



