/** CSS reset */
html, body {
	margin: 0px; padding: 0px;
	width: 100vw; height: 100vh;
	color: rgb( 166, 178, 192 );
}

html {
	font-size: 10vmax;
	direction: rtl;
	font-family: Dastnevis;
}

@media screen and ( min-width: 750px ), screen and ( min-height: 750px ) {
	html {
		font-size: 75px;
	}
}

* {
	-webkit-box-sizing: border-box;
	        box-sizing: border-box;
	text-align: center;
}

*::-webkit-scrollbar {
	display: none;
}


/** Fonts */
@font-face {
	font-family: Dastnevis;
	font-style: normal;
	src: url( '/assets/fonts/DastNevis.otf' ) format( 'opentype' );
}

@font-face {
	font-family: IranSans;
	font-style: normal;
	src: url( '/assets/fonts/IranSans.woff' ) format( 'woff' );
}

@font-face {
	font-family: Behdad;
	font-style: normal;
	src: url( '/assets/fonts/Behdad-Regular.woff2' ) format( 'woff2' );
}

@font-face {
	font-family: Nazanin;
	font-style: normal;
	src: url( '/assets/fonts/Nazanin-Regular.woff2' ) format( 'woff2' );
}

@font-face {
	font-family: Noon;
	font-style: normal;
	src: url( '/assets/fonts/Noon-Regular.woff2' ) format( 'woff2' );
}

@font-face {
	font-family: Yekan;
	font-style: normal;
	src: url( '/assets/fonts/Yekan-Regular.woff2' ) format( 'woff2' );
}

@font-face {
	font-family: Ganjnameh;
	font-style: normal;
	src: url( '/assets/fonts/Ganjnameh-Regular.woff2' ) format( 'woff2' );
}


/** Common classes */
.flex, .flex-row {
	display: -ms-flexbox;
	display: flex;
	-ms-flex-direction: row;
	    flex-direction: row;
}

.flex-column {
	display: -ms-flexbox;
	display: flex;
	-ms-flex-direction: column;
	    flex-direction: column;
}

.flex-center {
	-ms-flex-pack: center;
	    justify-content: center;
	-ms-flex-align: center;
	    align-items: center;
	-ms-flex-line-pack: center;
	    align-content: center;
}

.full-w  { width : 100%;               }
.full-h  { height: 100%;               }
.full-wh { width : 100%; height: 100%; }

.spacer { -ms-flex: 1 1 auto; flex: 1 1 auto; }

.transition-all {
	-webkit-transition: all linear 100ms;
	-o-transition: all linear 100ms;
	transition: all linear 100ms;
}

.ltr {
	direction: ltr;
}


/** Components */
.button {
	-webkit-transition: 100ms all linear;
	-o-transition: 100ms all linear;
	transition: 100ms all linear;
	background: rgb( 39, 43, 51 );
	border: 2px solid rgb( 166, 178, 192 );
	color: rgb( 166, 178, 192 );
	outline: none;
}

.button:hover, .button:active {
	background: rgb( 166, 178, 192 );
	color: rgba( 39, 43, 51, 0.9 );
}



